import uniTransition from '@/components/uni-transition/uni-transition.vue';
import viewHighlight from '@/components/view-highlight.vue';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';

import { mapState, mapMutations } from 'vuex';

export default {
	components: { uniTransition, viewHighlight,uniSegmentedControl },
	name: 'color-picker',
	props: {
		tabItems: {
			type: Array
		},
		colorSets: {
			type: Array
		},
		defColor: {
			type: String,
			default: 'transparent'
		},
		w: {
			type: Number,
			default: 50
		},
		h: {
			type: Number,
			default: 50
		}
	},
	data() {
		return {
			// tabItems:['a','b','c'],
			showColorPicker: false,
			color: 'transparent',
			style: '',
			hl: false,
			tabIndex: 0,
			transfromClass: {
				position: 'absolute',
				bottom: 0,
				top: 0,
				left: 0,
				right: 0,
				width: '100%',
				height: '100%',
				margin: 'auto',
				display: 'flex',
				'justify-content': 'center',
				'align-items': 'center',
				'background-color': 'rgba(251, 251, 251, 0.9)'
			}
		};
	},
	methods: {
		getColor(c) {
			this.color = c;
		},
		onChangeColorSetTabItem: function(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		},
		transIsDone() {
			
		},
		onDone() {
			
		},
		onChangeTabItem(e) {
		if (this.tabIndex !== e.currentIndex) {
			this.tabIndex = e.currentIndex;
		}		
		}
	}
};
